<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import HistoryViewModal from "../Include/history_view";
import EditModal from "./Modal/edit_modal";
export default {
  name: "costEstimate",
  components: {
    HistoryViewModal,
    EditModal,
  },
  props: ["editFormObject", "tab"],
  data: () => ({
    editTable: "",
    dialogEdit: false,
    editFormIndex: -1,
    editedIndex: -1,

    table1: {
      tableTitle: "11.1.1 Investment cost.",
      tableName: "1111",
      tableHeader: [
        {
          text: "Investment",
          value: "investment",
          sortable: false,
          divider: true,
          total: null,
        },
        {
          text: "Total",
          value: "total",
          sortable: false,
