        },
        {
          text: "Fund Source",
          value: "fundSource",
          sortable: false,
          divider: true,
          total: 2,
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      tableModel: "1111",
      withTotal: 1,
    },
    table2: {
      tableTitle: "11.2.1 Annual Cost (Personnel Services).",
      tableName: "1121",
      tableHeader: [
        {
          text: "Items",
          value: "items",
          sortable: false,
          divider: true,
          total: null,
        },
        {
          text: "Total",
          value: "total",
          sortable: false,
          divider: true,
          total: 1,
        },
        {
          text: "Fund Source",
          value: "fundSource",
          sortable: false,
