          total: 2,
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      tableModel: "1121",
      withTotal: 1,
    },
    table3: {
      tableTitle: "11.2.2 Annual Cost (Maintenance and Operating Services).",
      tableName: "1122",
      tableHeader: [
        {
          text: "Items",
          value: "items",
          sortable: false,
          divider: true,
          total: null,
        },
        {
          text: "Total",
          value: "total",
          sortable: false,
          divider: true,
          total: 1,
        },
        {
          text: "Fund Source",
          value: "fundSource",
          sortable: false,
          divider: true,
          total: 2,
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      tableModel: "1122",
      withTotal: 1,
    },
    table4: {
      tableTitle: "11.3.1 Funding Options.",
      tableName: "1131",
      tableHeader: [
        {
          text: "Particular",
          value: "particular",
          sortable: false,
          divider: true,
          total: null,
        },
        {
          text: "Total",
          value: "total",
          sortable: false,
          divider: true,
          total: 1,
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      tableModel: "1131",
      withTotal: 1,
    },
    table5: {
      tableTitle: "11.4.1 Cost Evaluation and Comparison.",
      tableName: "1141",
      tableHeader: [
        {
          text: "Particular",
          value: "particular",
          sortable: false,
          divider: true,
          total: null,
        },
        {
          text: "Total",
          value: "total",
          sortable: false,
          divider: true,
          total: 1,
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      tableModel: "1141",
      withTotal: 1,
    },
    table6: {
      tableTitle: "11.5.1 Summary.",
      tableName: "1151",
      tableHeader: [
        {
          text: "Particular",
          value: "particular",
          sortable: false,
          divider: true,
          total: null,
        },
        {
          text: "Total",
          value: "total",
          sortable: false,
          divider: true,
          total: 1,
        },
        // { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [
        // {
        //   particular: "Total Investment Cost123",
        //   column0: "",
        //   column1: "",
        //   column2: "",
        //   column3: "",
        //   column4: "",
        //   column5: "",
        //   column6: "",
        //   column7: "",
        //   column8: "",
        //   column9: "",
        //   total: "",
        // },
        // {
        //   particular: "Total Annual Cost123",
        //   column0: "",
        //   column1: "",
        //   column2: "",
        //   column3: "",
        //   column4: "",
        //   column5: "",
        //   column6: "",
        //   column7: "",
        //   column8: "",
        //   column9: "",
        //   total: "",
        // },
      ],
      tableAddNew: 0,
      tableModel: "1151",
      withTotal: 1,
    },
  }),
  methods: {
    ...mapActions(["saveProfile11", "fetchProfile", "fetchPopulation"]),
    ...mapMutations([]),

    isNum(value, index, props) {
      var isValid = /^[0-9,.]*$/.test(value);
      if (isValid) {
        value = value.toString().replace(/,/g, ""); // remove commas from existing input
        this.table1.tableData[index][props] = this.numberWithCommas(value);
      } else {
        this.table1.tableData[index][props] = "";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    nonNullItems(table) {
      var gettable = this.setGetTable(table);
      let x = this["table" + gettable].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(key, table) {
      var gettable = this.setGetTable(table);
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this["table" + gettable].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },
    sumFieldPer(key, table) {
      var gettable = this.setGetTable(table);
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this["table" + gettable].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this["table" + gettable].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;
        if (isNaN(per)) {
          per = 0;
        }
        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },

    //START of TABLE FUNCTION
    addItem(tableName) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = tableName;
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(tableName, item) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = tableName;
      var gettable = this.setGetTable(tableName);
      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      //Finding index of row in table data
      this.editedIndex = this["table" + gettable].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item[e.model],
        }));

      this.dialogEdit = true;
    },
    deleteItem(tableName, item) {
      this.editTable = tableName;
      var gettable = this.setGetTable(tableName);
      this["table" + gettable].tableData.splice(
        this["table" + gettable].tableData.indexOf(item),
        1
      );
      // this.dialogDelete = true;
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editedIndex = -1;
      });
    },
    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
    },
    closeModal() {
      this.setDefaultEdit();
      this.dialogEdit = false;

      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },
    save() {
      var gettable = this.setGetTable(this.editTable);
      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this["table" + gettable].tableData[this.editedIndex],
          merged
        );
      } else {
        this["table" + gettable].tableData.push(merged);
      }

      this.setDefaultEdit();
      this.closeModal();
    },

    // END of TABLE FUNCTION

    setGetTable(table) {
      var gettable = -1;
      if (table == 1111) {
        gettable = 1;
      } else if (table == 1121) {
        gettable = 2;
      } else if (table == 1122) {
        gettable = 3;
      } else if (table == 1131) {
        gettable = 4;
      } else if (table == 1141) {
        gettable = 5;
      } else if (table == 1151) {
        gettable = 6;
      }

      return gettable;
    },

    pushProjectedYear() {
      for (let i = 0; i < 10; i++) {
        //For table and edit form 1111
        let x = i + 1;
        this.table1.tableHeader.splice(i + 1, 0, {
          text: "Year " + x,
          value: "column" + i,
          total: "column" + i,
          sortable: false,
