        tbl_1111: JSON.stringify(this.table1.tableData),
        tbl_1121: JSON.stringify(this.table2.tableData),
        tbl_1122: JSON.stringify(this.table3.tableData),
        tbl_1131: JSON.stringify(this.table4.tableData),
        tbl_1141: JSON.stringify(this.table5.tableData),
        tbl_1151: JSON.stringify(this.table6.tableData),
      });
    },

    getProfile() {
      this.fetchProfile({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
      }).then(() => {
        let profile = [];
        profile = JSON.stringify(this.allProfile);

        if (profile.length > 3) {
          let new_data = JSON.parse(profile);

          if (new_data.tbl_1111) {
            let table1data = JSON.parse(new_data.tbl_1111);
            this.table1.tableData = table1data;
          }

          if (new_data.tbl_1121) {
            let table2data = JSON.parse(new_data.tbl_1121);
            this.table2.tableData = table2data;
          }

          if (new_data.tbl_1122) {
            let table3data = JSON.parse(new_data.tbl_1122);
            this.table3.tableData = table3data;
          }
          if (new_data.tbl_1131) {
