      });
    },

    pushSummaryTable() {
      const new_array = [];
      var invCost = this.getInvCostTotal();
      var annCost = this.getAnnCostTotal();
      console.log(annCost);
      new_array.push(invCost);
      new_array.push(annCost);
      this.table6.tableData = new_array;
    },

    getInvCostTotal() {
      let new_data = this.table1.tableData;

      let props = [
        "column0",
        "column1",
        "column2",
        "column3",
        "column4",
        "column5",
        "column6",
        "column7",
        "column8",
        "column9",
        "total",
      ];

      new_data = new_data.map((e, i) => ({
        ...e,
        column0: e.column0 ? e.column0 : "0",
        column1: e.column1 ? e.column1 : "0",
        column2: e.column2 ? e.column2 : "0",
        column3: e.column3 ? e.column3 : "0",
        column4: e.column4 ? e.column4 : "0",
        column5: e.column5 ? e.column5 : "0",
        column6: e.column6 ? e.column6 : "0",
        column7: e.column7 ? e.column7 : "0",
        column8: e.column8 ? e.column8 : "0",
        column9: e.column9 ? e.column9 : "0",
        total: e.total ? e.total : "0",
      }));

      const result = new_data.reduce(
        (previousValue, currentValue) =>
          Object.fromEntries(
            props.map((column) => [
              column,
              (parseFloat(previousValue[column]) || 0) +
                parseFloat(currentValue[column].replace(/,/g, "")),
            ])
          ),
        {}
      );
      // console.log(result);

      let newRes = Object.assign(
        {},
        ...Object.keys(result).map((k) => ({
          ["particular"]: "Total Investment Cost",
          [k]: this.numberWithCommas(result[k].toFixed(2)),
        }))
      );

      return newRes;
    },
    getAnnCostTotal() {
      let personnel = this.table2.tableData;
      let Maintenance = this.table3.tableData;
      let props = [
        "column0",
        "column1",
        "column2",
        "column3",
        "column4",
        "column5",
        "column6",
        "column7",
        "column8",
        "column9",
        "total",
      ];
      var arr3 = [...personnel, ...Maintenance];

      arr3 = arr3.map((e, i) => ({
        ...e,
        column0: e.column0 ? e.column0 : "0",
        column1: e.column1 ? e.column1 : "0",
        column2: e.column2 ? e.column2 : "0",
        column3: e.column3 ? e.column3 : "0",
        column4: e.column4 ? e.column4 : "0",
        column5: e.column5 ? e.column5 : "0",
        column6: e.column6 ? e.column6 : "0",
        column7: e.column7 ? e.column7 : "0",
        column8: e.column8 ? e.column8 : "0",
        column9: e.column9 ? e.column9 : "0",
        total: e.total ? e.total : "0",
      }));

      const result = arr3.reduce(
        (previousValue, currentValue) =>
          Object.fromEntries(
            props.map((column) => [
              column,
              (parseFloat(previousValue[column]) || 0) +
                parseFloat(currentValue[column].replace(/,/g, "")),
            ])
          ),
        {}
      );
      // console.log(result);

      let newRes = Object.assign(
        {},
        ...Object.keys(result).map((k) => ({
          ["particular"]: "Total Annual Cost",
          [k]: this.numberWithCommas(result[k].toFixed(2)),
        }))
      );

      return newRes;
    },
  },
  computed: {
    ...mapGetters(["allProfile", "getStateSession", "allPopulation"]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  created() {
    this.getProfile();
    this.pushProjectedYear();
  },
  watch: {
    tab() {
      if (this.tab == 3) {
        this.getProfile();
      }
    },
    "table1.tableData": {
      handler(newValue, oldValue) {
        this.pushSummaryTable();
      },
      deep: true,
    },
    "table2.tableData": {
      handler(newValue, oldValue) {
        this.pushSummaryTable();
      },
      deep: true,
    },
    "table3.tableData": {
      handler(newValue, oldValue) {
        this.pushSummaryTable();
      },
      deep: true,
    },
  },
};
