<template>
  <v-container class="pa-1">
    <v-tabs dense v-model="tab" align-with-title class="">
      <v-tabs-slider color="yellow"></v-tabs-slider>

      <v-tab @click="changeTab(3)"> Evaluation </v-tab>
      <v-tab @click="changeTab(5)"> NSWMC Approved </v-tab>
      <v-tab @click="changeTab(6)"> Approved </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <RevList />
      </v-tab-item>
      <v-tab-item>
        <RevList2 />
      </v-tab-item>
      <v-tab-item>
        <RevList3 />
      </v-tab-item>
    </v-tabs-items>
  </v-container>
</template>
<script>
import RevList from "../components/Review/RevList";
import RevList2 from "../components/Review/RevList2";
import RevList3 from "../components/Review/RevList3";
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "ReviewList",
  created() {},
  data() {
    return { tab: null, limitRow: [10], getCondition: "rev" };
  },
  created() {
    // this.fetchPlanList({
    //   offset: this.tgetCurrentrow,
    //   limit: this.limitRow,
    //   status: 3,
    //   new_app: 1,
    //   action: this.getCondition,
    //   user_type: this.getStateSession["user_type"],
    //   user_id: this.getStateSession["id"],
    //   regionId: this.getStateSession["region_id"],
    //   group: this.getStateSession["group"],
    // });
    this.getPlanList([]); //USE MUTATION
    setTimeout(this.getplanlist, 1000);
  },
  methods: {
    ...mapActions(["fetchPlanList"]),
    ...mapMutations(["getPlanList"]),
    changeTab(stat) {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: stat,
        new_app: 1,
        action: this.getCondition,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      });
    },
    getplanlist() {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 3,
        new_app: 1,
        action: this.getCondition,
        user_type: this.getStateSession["user_type"],
        user_id: this.getStateSession["id"],
        regionId: this.getStateSession["region_id"],
        group: this.getStateSession["group"],
      });
    },
  },
  components: {
    RevList,
    RevList2,
    RevList3,
  },
  computed: {
    ...mapGetters(["tgetCurrentrow", "getStateSession"]),
  },
};
</script>