<template>
  <v-container class="pa-1">
    <v-card class="mx-auto pa-4">
      <template>
        <div class="text-h4">WACS Characteristics</div>
        <br />
        <div class="text-h5">Last updated:</div>
        <br />
        <div class="d-inline-flex pa-2"></div>

        <div>
          <v-data-table
            :headers="table1.tableHeader"
            :items="table1.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table1.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table1.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(table1.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(table1.tableName, item)"
              >
                mdi-pencil
              </v-icon>
              <v-icon small @click="deleteItem(table1.tableName, item)">
                mdi-delete
              </v-icon>
            </template>
            <template slot="body.append" v-if="table1.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table1.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table1.tableName) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table1.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table1.tableName)"
                  :key="x"
                >
                  {{ sumFieldPer(x, table1.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <br />

          <v-data-table
            :headers="table2.tableHeader"
            :items="table2.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table2.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table2.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(table2.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(table2.tableName, item)"
              >
                mdi-pencil
              </v-icon>
            </template>
            <template slot="body.append" v-if="table2.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table2.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table2.tableName) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table2.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table2.tableName)"
                  :key="x"
                >
                  {{ sumFieldPer(x, table2.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <br />
          <!-- TABLE 3 -->
          <v-data-table
            :headers="table3.tableHeader"
            :items="table3.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table3.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table3.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(table3.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(table3.tableName, item)"
              >
                mdi-pencil
              </v-icon>
            </template>
            <template slot="body.append" v-if="table3.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table3.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table3.tableName) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table3.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table3.tableName)"
                  :key="x"
                >
                  {{ sumFieldPer(x, table3.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <br />
          <!-- TABLE 4 -->
          <v-data-table
            :headers="table4.tableHeader"
            :items="table4.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table4.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table4.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(table4.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(table4.tableName, item)"
              >
                mdi-pencil
              </v-icon>
            </template>
            <template slot="body.append" v-if="table4.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table4.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table4.tableName) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table4.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table4.tableName)"
                  :key="x"
                >
                  {{ sumFieldPer(x, table4.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <v-dialog v-model="dialogEdit" :retain-focus="false">
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <template v-if="dialogEdit">
                    <EditModal
                      :dialogEdit="dialogEdit"
                      :editFormObject="editFormObject"
                      :editFormIndex="editFormIndex"
                    />
                  </template>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeModal">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Enter </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
        <br />
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="saveProfile">Save</v-btn>
        </v-card-actions>
      </template>
    </v-card>
  </v-container>
</template>
    

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import HistoryViewModal from "../Include/history_view";
import EditModal from "./Modal/edit_modal";
export default {
  name: "wacsProfile",
  components: {
    HistoryViewModal,
    EditModal,
  },
  props: ["editFormObject", "tab"],
  data: () => ({
    editTable: "",
    dialogEdit: false,
    editFormIndex: -1,
    editedIndex: -1,

    table1: {
      tableTitle:
        "4.1.2 Summary tables and figures showing the quantity and composition of disposed waste by sector.",
      tableName: "412",
      tableHeader: [
        {
          text: "Category / Sector",
          value: "catSector",
          sortable: false,
          total: null,
          divider: true,
        },
        {
          text: "Total (Kg/day)",
          value: "totalKg",
          sortable: false,
          total: "totalKg",
          divider: true,
        },
        {
          text: "Biodegradable Kg/day",
          value: "bioKg",
          sortable: false,
          total: "bioKg",
          divider: true,
        },
        {
          text: "%",
          value: "bioKgPer",
          sortable: false,
          total: "1",
          divider: true,
        },
        {
          text: "Recyclable Kg/day",
          value: "recKg",
          sortable: false,
          total: "recKg",
          divider: true,
        },
        {
          text: "%",
          value: "recKgPer",
          sortable: false,
          total: "2",
          divider: true,
        },
        {
          text: "RWP For Diversion Kg/day",
          value: "rwpKg",
          sortable: false,
          total: "rwpKg",
          divider: true,
        },
        {
          text: "%",
          value: "rwpKgPer",
          sortable: false,
          total: "3",
          divider: true,
        },
        {
          text: "For Disposal Kg/day",
          value: "forDisKg",
          sortable: false,
          total: "forDisKg",
          divider: true,
        },
        {
          text: "%",
          value: "forDisKgPer",
          sortable: false,
          total: "4",
          divider: true,
        },
        {
          text: "Special Kg/day",
          value: "specKg",
          sortable: false,
          total: "specKg",
          divider: true,
        },
        {
          text: "%",
          value: "specKgPer",
          sortable: false,
          total: "5",
          divider: true,
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      withTotal: 1,
      withTotalPer: 1,
      tableModel: "412",
    },
    table2: {
      tableTitle:
        "4.2.1 Quantity of waste currently recycled and composted based on existing information.",
      tableName: "421",
      tableHeader: [
        {
          text: "Category / Sector",
          value: "catSector",
          sortable: false,
          total: null,
        },
        {
          text: "Biodegradable Kg/day",
          value: "bioKg",
          sortable: false,
          total: "bioKg",
        },
        // {
        //   text: "%",
        //   value: "bioKgPer",
        //   sortable: false,
        //   total: "1",
        // },
        {
          text: "Recyclable Kg/day",
          value: "recKg",
          sortable: false,
          total: "recKg",
        },
        // {
        //   text: "%",
        //   value: "recKgPer",
        //   sortable: false,
        //   total: "2",
        // },
        {
          text: "RWPD Kg/day",
          value: "rwpKg",
          sortable: false,
          total: "rwpKg",
        },
        // {
        //   text: "%",
        //   value: "rwpKgPer",
        //   sortable: false,
        //   total: "3",
        // },

        // { text: "Action", value: "Action", sortable: false },
      ],
      tableData: [],
      tableAddNew: 0,
      tableModel: "421",
      withTotal: 1,
      withTotalPer: 1,
      nonAction: "1",
    },
    table3: {
      tableTitle: "4.3.1 Quantity of waste generated (disposed + diverted).",
      tableName: "431",
      tableHeader: [
        {
          text: "Classification of Waste",
          value: "classWaste",
          sortable: false,
          total: null,
        },
        {
          text: "Generated (kg/ day)",
          value: "generated",
          sortable: false,
          total: "generated",
        },
        {
          text: "Percentage (%)",
          value: "percentage",
          sortable: false,
          total: "percentage",
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [
        {
          classWaste: "Diverted (Biodegradables + Recyclables)",
          generated: "",
          percentage: "",
        },
        {
          classWaste: "Disposed (Residuals + Special Waste)",
          generated: "",
          percentage: "",
        },
      ],
      tableAddNew: 0,
      withTotal: 1,
      tableModel: "431",
    },
    table4: {
      tableTitle:
        "4.3.2 Projection of quantity of waste generated based on population projections.",
      tableName: "432",
      tableHeader: [
        {
          text: "Year",
          value: "year",
          sortable: false,
          total: null,
          divider: true,
        },
        {
          text: "Population",
          // value: "popul",
          value: "projPopulation",
          sortable: false,
          total: 123,
          divider: true,
        },

        {
          text: "Total Waste Generation(kg)",
          value: "wasteGen",
          sortable: false,
          total: "wasteGen",
          // total: "totalKg",
          divider: true,
        },
        {
          text: "Waste to be diverted",
          value: "wasteDiv",
          sortable: false,
          divider: true,
        },
        {
          text: "(%)",
          value: "wasteDivPer",
          sortable: false,
          divider: true,
        },
        {
          text: "Target waste to be disposed",
          value: "wasteDis",
          sortable: false,
          divider: true,
        },
        {
          text: "(%)",
          value: "wasteDisPer",
          sortable: false,
          divider: true,
        },
        {
          text: "Biodegradables",
          value: "wacsbio",
          sortable: false,
          divider: true,
        },
        {
          text: "(%)",
          value: "wacsbioPer",
          sortable: false,
          divider: true,
        },
        {
          text: "Recyclables",
          value: "wacsrec",
          sortable: false,
          divider: true,
        },
        {
          text: "(%)",
          value: "wacsrecPer",
          sortable: false,
          divider: true,
        },
        {
          text: "RWPD",
          value: "wacsrwpd",
          sortable: false,
          divider: true,
        },
        {
          text: "(%)",
          value: "wacsrwpdPer",
          sortable: false,
          divider: true,
        },
        {
          text: "For Disposal",
          value: "wacsdis",
          sortable: false,
          divider: true,
        },
        {
          text: "(%)",
          value: "wacsdisPer",
          sortable: false,
          divider: true,
        },
        {
          text: "Special",
          value: "wacsspec",
          sortable: false,
          divider: true,
        },
        {
          text: "(%)",
          value: "wacsspecPer",
          sortable: false,
          divider: true,
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 0,
      tableModel: "432",
    },
  }),
  methods: {
    ...mapActions(["saveProfile4", "fetchProfile", "fetchPopulation"]),
    ...mapMutations([]),

    isNum(value, index, props) {
      var isValid = /^[0-9,.]*$/.test(value);
      if (isValid) {
        value = value.toString().replace(/,/g, ""); // remove commas from existing input
        this.table1.tableData[index][props] = this.numberWithCommas(value);
      } else {
        this.table1.tableData[index][props] = "";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    nonNullItems(table) {
      var gettable = this.setGetTable(table);
      let x = this["table" + gettable].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(key, table) {
      var gettable = this.setGetTable(table);
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this["table" + gettable].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },
    sumFieldPer(key, table) {
      var gettable = this.setGetTable(table);
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this["table" + gettable].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this["table" + gettable].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;
        if (isNaN(per)) {
          per = 0;
        }
        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },

    // TABLE FUNCTION
    addItem(tableName) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = tableName;
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(tableName, item) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = tableName;
      var gettable = this.setGetTable(tableName);
      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      //Finding index of row in table data
      this.editedIndex = this["table" + gettable].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item[e.model],
        }));

      this.dialogEdit = true;
    },
    deleteItem(tableName, item) {
      this.editTable = tableName;
      var gettable = this.setGetTable(tableName);
      this["table" + gettable].tableData.splice(
        this["table" + gettable].tableData.indexOf(item),
        1
      );
      this.table2.tableData.splice(
        this["table" + gettable].tableData.indexOf(item),
        1
      );
      // this.dialogDelete = true;
    },
    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
    },
    closeModal() {
      this.setDefaultEdit();
      this.dialogEdit = false;

      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },
    save() {
      var gettable = this.setGetTable(this.editTable);
      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this["table" + gettable].tableData[this.editedIndex],
          merged
        );
      } else {
        this["table" + gettable].tableData.push(merged);
      }

      //=============FOR COPY OF TABLE 412 TO TABLE 421 ====================//

      if (gettable == 1) {
        if (this.editedIndex > -1) {
          Object.assign(this.table2.tableData[this.editedIndex], merged);
        } else {
          this.table2.tableData.push(merged);
        }
      }

      //=============FOR COPY OF TABLE 412 TO TABLE 421 ====================//

      this.setDefaultEdit();
      this.closeModal();
    },

    // TABLE FUNCTION

    setGetTable(table) {
      var gettable = -1;
      if (table == 412) {
        gettable = 1;
      } else if (table == 421) {
        gettable = 2;
      } else if (table == 431) {
        gettable = 3;
      } else if (table == 432) {
        gettable = 4;
      }

      return gettable;
    },

    pushProjectedYear() {
      for (let i = 1; i <= 10; i++) {
        //For table 432
        this.table4.tableData.push({
          year: "Year " + i,
          projPopulation: "",
          wasteGen: "",
          wacsbio: "",
          wacsbioPer: "",
          wacsdis: "",
          wacsdisPer: "",
          wacsrec: "",
          wacsrecPer: "",
          wacsrwpd: "",
          wacsrwpdPer: "",
          wacsspec: "",
          wacsspecPer: "",
          wasteDis: "",
          wasteDisPer: "",
          wasteDiv: "",
          wasteDivPer: "",
        });
      }
    },

    saveProfile() {
      this.saveProfile4({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
        wacs: JSON.stringify(this.table1.tableData),
        tbl_431: JSON.stringify(this.table3.tableData),
        tbl_432: JSON.stringify(this.table4.tableData),
      });
    },

    getProfile() {
      this.fetchProfile({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
      }).then(() => {
        let profile = [];
        profile = JSON.stringify(this.allProfile);

        if (profile.length > 3) {
          let new_data = JSON.parse(profile);

          if (new_data.wacs) {
            let table1data = JSON.parse(new_data.wacs);
            this.table1.tableData = table1data;

            let table2data = JSON.parse(new_data.wacs);
            this.table2.tableData = table2data;
          }

          if (new_data.tbl_431) {
            let table3data = JSON.parse(new_data.tbl_431);
            this.table3.tableData = table3data;
          }
          if (new_data.tbl_432) {
            let table4data = JSON.parse(new_data.tbl_432);
            this.table4.tableData = table4data;
          }

          // this.getPopulation();
          this.pushPopulation(new_data.proj_bar_popul);
        }
      });
    },

    pushPopulation(data) {
      let new_data = JSON.parse(data);

      if (new_data != undefined) {
        let props = [
          "column0",
          "column1",
          "column2",
          "column3",
          "column4",
          "column5",
          "column6",
          "column7",
          "column8",
          "column9",
        ];
        new_data = new_data.map((e, i) => ({
          ...e,
          column0: e.column0 ? e.column0 : "0",
          column1: e.column1 ? e.column1 : "0",
          column2: e.column2 ? e.column2 : "0",
          column3: e.column3 ? e.column3 : "0",
          column4: e.column4 ? e.column4 : "0",
          column5: e.column5 ? e.column5 : "0",
          column6: e.column6 ? e.column6 : "0",
          column7: e.column7 ? e.column7 : "0",
          column8: e.column8 ? e.column8 : "0",
          column9: e.column9 ? e.column9 : "0",
        }));
        const result = new_data.reduce(
          (previousValue, currentValue) =>
            Object.fromEntries(
              props.map((column) => [
                column,
                (parseFloat(previousValue[column]) || 0) +
                  parseFloat(currentValue[column].replace(/,/g, "")),
              ])
            ),
          {}
        );
        console.log(result);
        // const new_array = [];
        // new_array.push(result);

        this.table4.tableData = this.table4.tableData.map((e, i) => ({
          ...e,
          projPopulation: this.numberWithCommas(result["column" + i]),
        }));
      }
    },
  },
  computed: {
    ...mapGetters(["allProfile", "getStateSession", "allPopulation"]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  created() {
    this.getProfile();
    this.pushProjectedYear();
  },
  watch: {
    tab() {
      if (this.tab == 1) {
        this.getProfile();
      }
    },
  },
};
</script>