<template>
  <v-container class="pa-1">
    <v-card class="mx-auto pa-4">
      <template>
        <div class="text-h4">City Profile</div>
        <br />
        <div class="text-h5">Last updated:</div>
        <br />
        <div class="d-inline-flex pa-2">
          <v-text-field
            dense
            label="Growth Rate"
            v-model="growthRate"
            outlined
            @keyup="setgrowthrate()"
          ></v-text-field>
        </div>

        <div>
          <!-- TABLE 1 -->
          <v-data-table
            :headers="table1.tableHeader"
            :items="table1.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table1.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
              </v-toolbar>
            </template>

            <template v-slot:[`item.totalPopulation`]="props">
              <v-edit-dialog
                large
                @save="
                  savefunction1(
                    props.item.totalPopulation,
                    props.index,
                    props.header.value
                  )
                "
              >
                <div>{{ props.item.totalPopulation }}</div>
                <template v-slot:input>
                  <div class="mt-4 text-h6">Update Total Population</div>
                  <v-text-field
                    v-model="props.item.totalPopulation"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                    @keyup="
                      isNum(
                        props.item.totalPopulation,
                        props.index,
                        props.header.value
                      )
                    "
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:[`item.growthRate`]="props">
              <v-edit-dialog
                large
                @save="
                  savefunction1(
                    props.item.growthRate,
                    props.index,
                    props.header.value
                  )
                "
              >
                <div>{{ props.item.growthRate }}</div>
                <template v-slot:input>
                  <div class="mt-4 text-h6">Update Growth Rate</div>
                  <v-text-field
                    v-model="props.item.growthRate"
                    label="Edit"
                    single-line
                    counter
                    top
                    autofocus
                    @keyup="
                      isNum(
                        props.item.growthRate,
                        props.index,
                        props.header.value
                      )
                    "
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <!-- :return-value.sync="props.item.numberHousehold" -->
            <template v-slot:[`item.numberHousehold`]="props">
              <v-edit-dialog large>
                <div>{{ props.item.numberHousehold }}</div>
                <template v-slot:input>
                  <div class="mt-4 text-h6">Update Number Household</div>
                  <v-text-field
                    v-model="props.item.numberHousehold"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                    @keyup="
                      isNum(
                        props.item.numberHousehold,
                        props.index,
                        props.header.value
                      )
                    "
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template slot="body.append" v-if="table1.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table1.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table1.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <br />

          <!-- TABLE 2 -->
          <v-data-table
            :headers="table2.tableHeader"
            :items="table2.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table2.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
              </v-toolbar>
            </template>
            <template slot="body.append" v-if="table2.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table2.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table2.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <br />

          <!-- TABLE 3 -->
          <v-data-table
            :headers="table3.tableHeader"
            :items="table3.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table3.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table3.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(table3.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(table3.tableName, item)"
              >
                mdi-pencil
              </v-icon>
              <v-icon small @click="deleteItem(table3.tableName, item)">
                mdi-delete
              </v-icon>
            </template>
            <template slot="body.append" v-if="table3.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table3.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table3.tableName) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table3.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table3.tableName)"
                  :key="x"
                >
                  {{ sumFieldPer(x, table3.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <br />

          <!-- TABLE 4 -->
          <v-data-table
            :headers="table4.tableHeader"
            :items="table4.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table4.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table4.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(table4.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(table4.tableName, item)"
              >
                mdi-pencil
              </v-icon>
              <v-icon small @click="deleteItem(table4.tableName, item)">
                mdi-delete
              </v-icon>
            </template>
            <template slot="body.append" v-if="table4.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table4.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table4.tableName) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table4.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table4.tableName)"
                  :key="x"
                >
                  {{ sumFieldPer(x, table4.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <br />

          <!-- TABLE 5 -->
          <v-data-table
            :headers="table5.tableHeader"
            :items="table5.tableData"
            dense
            :items-per-page="-1"
            :hide-default-footer="true"
            class="elevation-1 my_class2"
            style="white-space: pre-line"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title class="text-subtitle-2">{{
                  table5.tableTitle
                }}</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <template v-if="table5.tableAddNew > 0">
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    @click="addItem(table5.tableName)"
                  >
                    New Item
                  </v-btn>
                </template>
              </v-toolbar>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(table5.tableName, item)"
              >
                mdi-pencil
              </v-icon>
              <v-icon small @click="deleteItem(table5.tableName, item)">
                mdi-delete
              </v-icon>
            </template>
            <template slot="body.append" v-if="table5.withTotal == 1">
              <tr>
                <td class="text-subtitle-2">Total</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table5.tableName)"
                  :key="x"
                >
                  {{ sumField(x, table5.tableName) }}
                </td>
              </tr>
            </template>
            <template slot="body.append" v-if="table5.withTotalPer == 1">
              <tr>
                <td class="text-subtitle-2">Total %</td>
                <td
                  class="text-subtitle-2"
                  v-for="x in nonNullItems(table5.tableName)"
                  :key="x"
                >
                  {{ sumFieldPer(x, table5.tableName) }}
                </td>
              </tr>
            </template>
          </v-data-table>

          <v-dialog v-model="dialogEdit" :retain-focus="false">
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <template v-if="dialogEdit">
                    <EditModal
                      :dialogEdit="dialogEdit"
                      :editFormObject="editFormObject"
                      :editFormIndex="editFormIndex"
                    />
                  </template>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeModal">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Enter </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">
            {{ snackText }}

            <template v-slot:action="{ attrs }">
              <v-btn v-bind="attrs" text @click="snack = false"> Close </v-btn>
            </template>
          </v-snackbar>
        </div>
        <br />
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" @click="saveProfile">Save</v-btn>
        </v-card-actions>
      </template>
    </v-card>
  </v-container>
</template>
    

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import EditModal from "./Modal/edit_modal";

export default {
  name: "chaptwo",
  components: {
    EditModal,
  },
  props: ["editFormObject", "tab", "chapters2"],
  data: () => ({
    gettable: -1,
    dialogEdit: false,
    editFormIndex: -1,
    editedIndex: -1,
    snack: false,
    snackColor: "",
    snackText: "",
    max25chars: (v) => v.length <= 25 || "Input too long!",
    pagination: {},
    growthRate: "",
    table1: {
      tableTitle: "Current population for each barangay.",
      tableName: "231",
      tableHeader: [
        {
          text: "Barangay ID",
          value: "barangay_id",
          sortable: false,
          align: " d-none",
          total: null,
        },
        {
          text: "Barangay",
          value: "barangay",
          sortable: false,
          divider: true,
          total: null,
        },
        {
          text: "Total Population (Current Year)",
          value: "totalPopulation",
          sortable: false,
          divider: true,
          total: "totalPopulation",
        },
        {
          text: "Growth Rate",
          value: "growthRate",
          sortable: false,
          divider: true,
          total: "1",
        },
        {
          text: "No. of Household",
          value: "numberHousehold",
          sortable: false,
          divider: true,
          total: "numberHousehold",
        },
        // { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 0,
      tableModel: "231",
      withTotal: 1,
    },
    table2: {
      tableTitle: "10 year Projected Population by Barangay.",
      tableName: "232",
      tableHeader: [
        {
          text: "Barangay ID",
          value: "barangay_id",
          sortable: false,
          align: " d-none",
          total: null,
        },
        {
          text: "Barangay",
          value: "barangay",
          sortable: false,
          divider: true,
          total: null,
        },
        // { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 0,
      tableModel: "232",
      withTotal: 1,
    },
    table3: {
      tableTitle: "2.4.1 List of industries within the city/municipality.",
      tableName: "241",
      tableHeader: [
        {
          text: "Nature of Business",
          value: "naturebusiness",
          sortable: false,
        },
        {
          text: "Total Number",
          value: "totalNumber",
          sortable: false,
          total: "totalNumber",
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      tableModel: "241",
      withTotal: 1,
    },
    table4: {
      tableTitle: "2.4.2	Land use map (URBAN).",
      tableName: "242",
      tableHeader: [
        {
          text: "Land Use Classification",
          value: "landUseClass",
          sortable: false,
        },
        {
          text: "Area (Hectares)",
          value: "area",
          sortable: false,
          total: "area",
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      withTotal: 1,
    },
    table5: {
      tableTitle: "2.4.3	Land use map (GENERAL).",
      tableName: "243",
      tableHeader: [
        {
          text: "Land Use Classification",
          value: "landUseClass",
          sortable: false,
        },
        {
          text: "Area (Hectares)",
          value: "area",
          sortable: false,
          total: "area",
        },
        { text: "Actions", value: "actions", sortable: false },
      ],
      tableData: [],
      tableAddNew: 1,
      withTotal: 1,
    },
  }),
  methods: {
    ...mapActions([
      "fetchNatureList",
      "fetchLandList",
      "fetchProfile",
      "fetchBarangay",
      "saveProfile2",
    ]),
    ...mapMutations([]),

    // TABLE FUNCTION
    addItem(tableName) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = tableName;
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );
      this.dialogEdit = true;
    },
    editItem(tableName, item) {
      // this.putIndex(chapterIndex, outlineIndex, tableIndex);
      this.editTable = tableName;
      var gettable = this.setGetTable(tableName);
      //Finding index of relative object from outline table to edit form object
      this.editFormIndex = this.editFormObject.findIndex(
        (item) => item.table === tableName
      );

      //Finding index of row in table data
      this.editedIndex = this["table" + gettable].tableData.indexOf(item);

      //Assign row data to Edit Forms
      let new_item = JSON.parse(JSON.stringify(item));
      this.editFormObject[this.editFormIndex].editableForm =
        this.editFormObject[this.editFormIndex].editableForm.map((e, i) => ({
          ...e,
          value: new_item[e.model],
        }));

      this.dialogEdit = true;
    },
    deleteItem(tableName, item) {
      this.editTable = tableName;
      var gettable = this.setGetTable(tableName);
      this["table" + gettable].tableData.splice(
        this["table" + gettable].tableData.indexOf(item),
        1
      );
      // this.dialogDelete = true;
    },
    setDefaultEdit() {
      let editFormLengthObject =
        this.editFormObject[this.editFormIndex].editableForm.length;
      for (let i = 0; i < editFormLengthObject; i++) {
        this.editFormObject[this.editFormIndex].editableForm[i].value = "";
      }
    },
    closeModal() {
      this.setDefaultEdit();
      this.dialogEdit = false;

      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editFormIndex = -1;
      });
    },
    save() {
      var gettable = this.setGetTable(this.editTable);
      let m = [];
      m = this.editFormObject[this.editFormIndex].editableForm.map((e) => ({
        [e.model]: e.value,
      }));
      let merged = m.reduce((obj, item) => ({ ...obj, ...item }), {});
      if (this.editedIndex > -1) {
        Object.assign(
          this["table" + gettable].tableData[this.editedIndex],
          merged
        );
      } else {
        this["table" + gettable].tableData.push(merged);
      }

      this.setDefaultEdit();
      this.closeModal();
    },

    // TABLE FUNCTION

    fetchBarangay2() {
      this.fetchBarangay(localStorage.getItem("city_id")).then(() => {
        this.pushBarangayPerTable(this.allBarangay);
      });
    },
    pushBarangayPerTable(barangays) {
      let new_item = JSON.parse(barangays);
      let barangayLength = new_item.length;
      console.log(new_item);

      for (let i = 0; i < barangayLength; i++) {
        //For table 231
        this.table1.tableData.push({
          barangay_id: new_item[i].id,
          barangay: new_item[i].name,
          totalPopulation: "",
          growthRate: "",
          numberHousehold: "",
        });

        this.table2.tableData.push({
          barangay_id: new_item[i].id,
          barangay: new_item[i].name,
          column0: "",
          column1: "",
          column2: "",
          column3: "",
          column4: "",
          column5: "",
          column6: "",
          column7: "",
          column8: "",
          column9: "",
        });
      }
    },

    pushProjectedYear() {
      for (let i = 0; i < 10; i++) {
        let x = i + 1;
        this.table2.tableHeader.splice(i + 2, 0, {
          text: "Year " + x,
          value: "column" + i,
          sortable: false,
          divider: true,
          total: "column" + i,
        });
      }
    },

    pushLandClass(land) {
      let new_item = JSON.parse(JSON.stringify(land));
      let landLength = new_item.length;
      for (let i = 0; i < landLength; i++) {
        this.editFormObject[3].editableForm[0].items.push(new_item[i].class);
        this.editFormObject[4].editableForm[0].items.push(new_item[i].class);
      }
    },
    pushNature(business) {
      let new_item = JSON.parse(JSON.stringify(business));
      let landLength = new_item.length;
      for (let i = 0; i < landLength; i++) {
        this.editFormObject[2].editableForm[0].items.push(
          new_item[i].nature_of_business
        );
      }
    },

    isNum(value, index, props) {
      var isValid = /^[0-9,.]*$/.test(value);
      if (isValid) {
        value = value.toString().replace(/,/g, ""); // remove commas from existing input
        this.table1.tableData[index][props] = this.numberWithCommas(value);
      } else {
        this.table1.tableData[index][props] = "";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      9, 150.2;
      return parts.join(".");
    },
    nonNullItems(table) {
      var gettable = this.setGetTable(table);
      let x = this["table" + gettable].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(key, table) {
      var gettable = this.setGetTable(table);
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this["table" + gettable].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },

    savefunction1(value, index, val2) {
      console.log(this.table2.tableData);
      if (val2 == "totalPopulation") {
        this.table2.tableData[index].column0 = this.numberWithCommas(value);
      }

      if (val2 == "totalPopulation" || val2 == "growthRate") {
        if (
          this.table1.tableData[index].totalPopulation != "" &&
          this.table1.tableData[index].growthRate != ""
        ) {
          this.projcomp(index);
        }
      }
      this.snack = true;
      this.snackColor = "success";
      this.snackText = "Data saved";
    },

    projcomp(index) {
      var currPopul = this.table1.tableData[index].totalPopulation;
      var growthRate = this.table1.tableData[index].growthRate;
      currPopul = currPopul.replace(/,/g, "");

      for (let i = 1; i < 10; i++) {
        let x = i + 1;
        let add_growth = (growthRate / 100) * parseInt(currPopul);
        currPopul = parseInt(currPopul) + parseInt(add_growth);
        this.table2.tableData[index]["column" + i] =
          this.numberWithCommas(currPopul);
      }
    },

    setgrowthrate() {
      this.table1.tableData = this.table1.tableData.map((e, i) => ({
        ...e,
        growthRate: this.growthRate,
      }));
    },

    saveProfile() {
      this.saveProfile2({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
        growth_rate: this.growthRate,
        bar_popul: JSON.stringify(this.table1.tableData),
        proj_bar_popul: JSON.stringify(this.table2.tableData),
        list_industries_within_city: JSON.stringify(this.table3.tableData),
        land_used_map_urban: JSON.stringify(this.table4.tableData),
        land_used_map_general: JSON.stringify(this.table5.tableData),
      });
    },

    getProfile() {
      this.table1.tableData = [];
      this.table2.tableData = [];
      this.fetchProfile({
        user: this.getStateSession["iis_employee_number"],
        lgu: localStorage.getItem("city_id"),
      }).then(() => {
        let profile = [];
        profile = JSON.stringify(this.allProfile);
        if (this.table2.tableHeader.length == 2) {
          this.pushProjectedYear();
        }
        if (profile.length > 3) {
          let new_data = JSON.parse(profile);
          this.growthRate = new_data["growth_rate"];

          if (new_data.bar_popul) {
            let curPop = JSON.parse(new_data.bar_popul);
            this.table1.tableData = curPop;
          } else {
            this.fetchBarangay2();
          }

          if (new_data.proj_bar_popul) {
            let projPop = JSON.parse(new_data.proj_bar_popul);
            this.table2.tableData = projPop;
          }

          if (new_data.list_industries_within_city) {
            let dataTable3 = JSON.parse(new_data.list_industries_within_city);
            this.table3.tableData = dataTable3;
          }

          if (new_data.land_used_map_urban) {
            let dataTable4 = JSON.parse(new_data.land_used_map_urban);
            this.table4.tableData = dataTable4;
          }

          if (new_data.land_used_map_general) {
            let dataTable5 = JSON.parse(new_data.land_used_map_general);
            this.table5.tableData = dataTable5;
          }

          // if (curPop == undefined || projPop == undefined) {
          //   this.fetchBarangay2();
          // }
        } else {
          this.fetchBarangay2();
        }

        this.fetchLandList({
          offset: 0,
          limit: 100,
        }).then(() => {
          this.pushLandClass(this.allLand);
        });

        this.fetchNatureList({
          offset: 0,
          limit: 100,
        }).then(() => {
          this.pushNature(this.allNature);
        });
      });
    },

    setGetTable(table) {
      var gettable = -1;
      if (table == 231) {
        gettable = 1;
      } else if (table == 232) {
        gettable = 2;
      } else if (table == 241) {
        gettable = 3;
      } else if (table == 242) {
        gettable = 4;
      } else if (table == 243) {
        gettable = 5;
      }

      return gettable;
    },
  },
  computed: {
    ...mapGetters([
      "allNature",
      "allLand",
      "allProfile",
      "allBarangay",
      "getStateSession",
    ]),
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },
  created() {
    setTimeout(this.getProfile, 1000);
  },
  watch: {
    tab() {
      if (this.tab == 0) {
        this.getProfile();
      }
    },
  },
};
</script>